body {
	font-family: Helvetica, Arial, sans-serif;
}

html,
body {
	margin: 0;
	padding: 0;
	height: 100%;
}

#root {
	min-height: 100%;
	position: relative;
}

div.content-section {
	/* padding bottom is equal to the footer's height plus actual padding */
	padding: 0 0 100px;
}

.header,
div.list-container,
.footer .footer-text {
	margin: 0 auto;
	max-width: 400px;
}

.header,
.list-container {
	overflow: auto;
}

.nav-links {
	margin: 0 0 20px;
}

.nav-links p,
.nav-links ul {
	float: left;
	list-style: none;
}

.nav-links ul {
	padding: 0;
}

.nav-links li {
	float: left;
}

.nav-link {
	padding: 0 4px 0;
}

.nav-link.active {
	color: black;
	cursor: default;
	text-decoration: none;
}

.nav-link.inactive {
	color: blue;
}

.todo-form input {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.todo-form .text-input {
	margin-left: 20px;
	width: 300px;
}

.todo-form .button {
	width: 70px;
	margin: 0 0 0 10px;
}

span.error-list {
	color: red;
}

ul.todo-list {
	padding: 0;
}

.todo-list li {
	min-height: 25px;
}

.todo-list li.in-process {
	background-color: rgba(255, 255, 0, 0.2);
}

.todo-list li.failed {
	background-color: rgba(255, 0, 0, 0.2);
}

.todo-list li.success {
	background-color: rgba(0, 255, 0, 0.2);
}

.todo-item {
	list-style: none;
}

.todo-item input {
	cursor: pointer;
}

.todo-item p {
	display: inline-block;
	max-width: 300px;
	vertical-align: top;
	margin: 0 0 10px;
	cursor: pointer;
}

.todo-item button {
	width: 70px;
	float: right;
	cursor: pointer;
}

.todo-item button:disabled {
	cursor: default;
}

.footer {
	background: #eee;
	width: 100%;
	height: 80px; /* once you change the footer's height don't forget to change the above container's padding bottom to the same new value */
	position: absolute;
	bottom: 0;
	left: 0;
}

.footer .footer-text {
	margin: 0 auto;
	padding: 15px 15px 20px;
}



